import React, {useContext} from 'react';
import '@styles/base/pages/page-auth.scss';
import {compose} from "redux";
import {connect} from "react-redux";
import {login} from "../../../../redux/reducers/auth-reducer";
import Login from "./Login";
import {useHistory} from "react-router-dom";
import {AbilityContext} from "../../../../utility/context/Can";
import logo from '../../../../assets/images/logo.png';
import bg from '@src/assets/images/pages/login-v2-dark.svg';

const LoginContainer = (props) => {
    const history = useHistory();
    const ability = useContext(AbilityContext);
    const onSubmit = (formData) => {
        props.login(formData, history, ability);
    }
    return (
        <Login logo={logo} message={props.messageAuthForm} source={bg} onSubmit={onSubmit}  />
    );
}

const mapStateToProps = (state) => ({
    messageAuthForm: state.appInit.messageAuthForm
});

export default compose(connect(mapStateToProps, {login}))(LoginContainer);
