import {createField, Input} from "../../../../FormsControl/FormsControls";
import {isLatin, isSymbols, LengthLogin, LengthPwd, required} from "../../../../utils/validators/validators";
import {reduxForm} from "redux-form";
import React, {useState} from "react";
import {Button, Col, Row} from "reactstrap";
import s from '../Authentication.module.css';
import ReCaptcha from "../ReCaptcha";

const LoginForm = ({invalid, handleSubmit}) => {
    const [captchaValue, setCaptchaValue] = useState(null)
    const onChange = (value) => {
        setCaptchaValue(value);
    }
    return (
        <form className='auth-login-form mt-2' onSubmit={handleSubmit}>
            {
                createField([required, LengthLogin, isSymbols, isLatin], Input, 'Логин',
                    {
                        placeholder: 'Введите логин',
                        name: 'user_login',
                        id: 'login',
                        type: 'text',
                        autoComplete: 'on',
                        className: 'input-group-merge is-touched is-dirty av-valid form-control',
                        required: '',
                        autoFocus: ''
                    })
            }
            {
                createField([required, LengthPwd, isSymbols, isLatin], Input, 'Пароль',
                    {
                        placeholder: 'Введите пароль',
                        name: 'user_pwd',
                        id: 'password',
                        type: 'password',
                        autoComplete: 'on',
                        className: 'input-group-merge is-touched is-dirty av-valid form-control',
                        required: ''
                    })
            }
            <Row>
                <Col xs={12} className='mb-1 d-flex justify-content-center'>
                    <ReCaptcha onChange={onChange} />
                </Col>
            </Row>
            <Button.Ripple color={'white'} className={s.authButton} block disabled={invalid || captchaValue === null}>
                Войти
            </Button.Ripple>
        </form>
    );
}

const LoginFormContainer = reduxForm({form: 'login'})(LoginForm);

export default LoginFormContainer;